.video{
    &__title{
        color: #ccc;
        margin-left: 266px;
        margin-top: 18px;
        font-size: 24px;
        font-family: "Outfit";
    }
    &__text{
        color: #ccc;
        font-size: 17px;
        font-family: "Outfit";
        margin-left: 266px;
        margin-top: 11px;
    }
    &__span{
        display: flex;
        margin-right: 12px;
        width: 60px;
        height: 20px;
        background-color: #EBEBEB;
        padding: 10px 20px;
        padding-bottom: 15px;
        border-radius: 20px;
        cursor: pointer;
    }
    &__user--span{
        display: flex;
        margin-left: -677px;
        margin-top: 88px;
    }
    &__user--img{
        width: 80px;
        height: 80px;
    }
    &__user--title{
        font-size: 26px;
        line-height: 33px;
        font-weight: 700;
        font-family: "Outfit";
        color: #ccc;
        margin-left: 11px;
    }
    &__user--text{
        margin-left: -190px;
        margin-top: 126px;
        font-size: 16px;
        line-height: 16px;
        font-weight: 500;
        font-family: "Outfit";
        color: #ccc;
    }
    &__users--text2{
        margin-left: -571px;
        margin-top: 235px;
        font-size: 15px;
        line-height: 17px;
        font-weight: 400;
        font-family: "Outfit";
        color: #ccc;
        margin-bottom: 15px;
        display: none;
    }
    &__users--text{
        margin-left: -139px;
        margin-top: 155px;
        font-size: 15px;
        line-height: 17px;
        font-weight: 400;
        font-family: "Outfit";
        color: #ccc;
        margin-bottom: 15px;
    }
    &__users--btn{
        width: 106px;
        height: 27px;
        background-color: transparent;
        border: none;
        color: #ccc;
        cursor: pointer;
        font-size: 15px;
        line-height: 17px;
        font-weight: 400;
        font-family: "Outfit";
        margin-top: 300px;
        margin-left: -600px;
    }
    &__div{
        display: flex;
        margin-left: 735px;
        margin-top: -18px;
    }
    &__like{
        color: #ccc;
        font-size: 26px;
    }
    &__dislike{
        color: #ccc;
        font-size: 26px;
    }
    &__counter{
        color: #ccc;
        font-size: 20px;
        font-family: "Outfit";
        margin-top: 0px;
        margin-left: 6px;
    }
    &__user--list{
        margin-left: 522px;
        margin-top: -444px;
        list-style-type: none;
    }
    &__user--item{
        margin-bottom: 22px;
        &:nth-child(4){
            margin-bottom: 0px;
        }
    }
    &__list--img{
        width: 330px;
        height: 200px;
        border-radius: 14px;
    }
    &__list--title{
        color: #ccc;
        font-weight: 600;
        font-size: 23px;
        margin-top: 6px;
        font-family: "Outfit";
        line-height: 24px;
        width: 333px;
    }
    &__iframe{
        width: 660px;
        height: 380px;
    }
}
@media screen and (max-width: 800px) and (min-width: 2px) {
    .video{
        &__iframe{
            width: 320px;
            height: 180px;
        }
        &__title{
            width: 300px;
        }
        &__span{
            margin-left: -290px;
            margin-top: 50px;
        }
        &__user--span{
            margin-left: -120px;
            margin-top: 133px;
        }
        &__user--text{
            margin-top: 166px;
        }
        &__user--list{
            margin-top: 250px;
            margin-left: 257px;
        }
        &__users--text{
            display: none;
        }
        &__users--text2{
            display: none;
        }
        &____users--btn{
            display: none;
        }
    }
    .footer{
        margin-top: 770px;
    }
    .nimadur{
        margin-left: -233px;
    }
}