.channel__image{
    width: 1000px;
    margin-left: 277px;
    margin-top: -16px;
    height: 200px;
    border-radius: 14px;
    display: block;
}
.channel__img{
    width: 70px;
    height: 70px;
    border-radius: 50%;
}
.channel__span{
    display: flex;
    margin-left: 300px;
    margin-top: 22px;
}
.channel__title{
    color: #ccc;
    margin-top: 20px;
    margin-left: 10px;
    font-size: 25px;
    font-weight: 600;
    font-family: "Outfit";
}
.channel__list{
    display: block;
    margin-left: 308px;
    margin-top: 55px;
    list-style-type: none;
}
.channel-span{
    display: flex;
}
.channel__api-title{
    width: 333px;
    font-weight: 600;
    font-family: "Outfit";
    color: #ccc;
    margin-left: 33px;
    font-size: 26px;
    line-height: 34px;
}
.channel__text{
    margin-left: 438px;
    margin-top: -122px;
    color: rgb(134, 134, 134);
    font-size: 14px;
    font-weight: 400;
    font-family: "Outfit";
}
.channel__item{
    &:nth-child(2) {
        display: none;
    }
}
.channel__user--list{
    margin-left: 1100px;
    margin-top: -299px;
}
.channel__user--item{
    display: flex;
    margin-top: 10px;
}
.channel__user--title{
    margin-left: 15px;
    margin-top: 10px;
}
.channel__user--navlink{
    text-decoration: none;
    display: flex;
}
.channel__user--recommended{
    margin-top: 6px;
    color: #ccc;
    font-size: 22px;
    font-family: "Outfit";
    font-weight: 500;
}
.channel__user--title{
    color: #ccc;
    font-size: 22px;
    font-weight: 500;
    font-family: "Outfit";
}
.yarkiy__channel--list{
    margin-top: 300px;
}
.yarkiy__channel--photos{
    color: #ccc;
    margin-top: -100px;
    font-size: 25px;
    font-weight: 600;
    margin-left: 333px;
    position: absolute;
    font-family: "Outfit";
}
.channel{
    overflow-y: hidden;
}
.footer{
    overflow-y: auto;
}
.yarkiy__list{
    overflow-x: auto;
}
.footer__item11{
    margin-bottom: 28px;
    &:nth-child(3){
        margin-bottom: 60px;
    }
}
@media screen and (max-width: 1000px) and (min-width: 10px) {
    .channel__image{
        display: none;
    }
    .channel{
        margin-left: -255px;
        margin-top: -30px;
    }
    .channel-span{
        display: block;
        margin-left: -5px;
    }
    .channel__text{
        display: none;
    }
    .channel__api-title{
        margin-left: 0px;
    }
    .channel__item {
        margin-bottom: 33px;
       &:nth-child(2) {
           display: block;
       }
    }
    .footer{
        margin-top: 700px;
    }
    .footer__item11{
        display: none;
        margin-left: 50px;
        &:nth-child(1) {
            display: block;
            margin-left: 55px;
        }
        &:nth-child(2) {
            display: block;
        }
        &:nth-child(3) {
            display: block;
        }
        &:nth-child(4) {
            display: block;
        }
    }
}